@import url(/node_modules/roboto-fontface/css/roboto/roboto-fontface.css);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

html {
  font-size: 62.5%; }

body {
  font-family: Roboto, sans-serif;
  font-weight: 400;
  font-size: 1.6rem;
  line-height: 1.7;
  color: #152935; }

h1 {
  font-weight: 300;
  font-size: 3.6rem;
  line-height: 4.5rem; }

h2 {
  font-size: 2.8rem;
  font-weight: 300;
  line-height: 3.5rem; }

h3 {
  font-size: 2rem;
  font-weight: 300;
  line-height: 2.5rem; }

h4 {
  font-size: 1.8rem;
  font-weight: 600;
  line-height: 2.3rem; }

h5 {
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 2rem; }

h6 {
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.8rem; }

p {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem; }

.caption {
  font-size: 1.2rem;
  font-weight: 400;
  line-height: 1.8rem; }

.legal {
  font-size: 1.1rem;
  font-weight: 400;
  line-height: 1.7rem; }

.divider {
  background-color: rgba(85, 150, 230, 0.1);
  height: 1px;
  width: 100%;
  margin: 1rem 0; }

.util-margin-top-large {
  margin-top: 3.2rem; }

.util-margin-bottom-large {
  margin-bottom: 3.2rem; }

.label {
  font-size: 1.4rem;
  font-weight: 600;
  line-height: 1.4rem;
  display: block;
  color: #152935;
  margin-bottom: .8rem;
  text-transform: capitalize; }
  .label--required {
    color: #bd1427;
    margin: 0 .4rem; }

.sidenav__item {
  border-left: 4px solid transparent;
  padding: 4px 16px; }
  .sidenav__item a {
    text-decoration: none;
    font-size: 1.4rem;
    font-weight: 600;
    color: #5a6872; }
  .sidenav__item:hover {
    border-left: 4px solid #30588c;
    background-color: rgba(85, 150, 230, 0.1); }
    .sidenav__item:hover a {
      color: #294c86; }
  .sidenav__item--active {
    border-left: 4px solid #30588c;
    color: #152935; }

.table {
  border-spacing: 0;
  margin-top: 20px;
  border-bottom: 1px solid #dfe3e6;
  width: 100%;
  /* th th:first-of-type th:last-of-type{
        border-top:1px solid $ui-04;
        border-right:1px solid $ui-04;
        border-left:1px solid $ui-04;
    }*/ }
  .table thead {
    background-color: #f4f7fb; }
  .table tbody {
    background-color: #ffffff; }
    .table tbody tr:nth-child(even) {
      background-color: #f4f7fb; }
  .table tr:hover td {
    background-color: rgba(85, 150, 230, 0.1);
    border-top: 1px solid #3d70b2;
    border-bottom: 1px solid #3d70b2; }
    .table tr:hover td:first-child {
      border-left: 1px solid #3d70b2; }
    .table tr:hover td:last-child {
      border-right: 1px solid #3d70b2; }
  .table td, .table th {
    text-align: left;
    padding: 1rem;
    border: 1px solid transparent;
    border-top: 1px solid #dfe3e6; }
    .table td:first-child, .table th:first-child {
      border-left: 1px solid #dfe3e6; }
    .table td:last-child, .table th:last-child {
      border-right: 1px solid #dfe3e6; }

.form__group {
  margin-bottom: 3.2rem; }

.form__hint, .form__error-box {
  font-size: 1.2rem;
  font-weight: 400;
  margin-bottom: .8rem; }

.form__error-box {
  color: #e0182d; }

.text-box {
  width: 100%;
  box-shadow: 0 1px 0 0 #5a6872;
  background-color: #f4f7fb;
  border: none;
  border-bottom: 1px solid transparent;
  height: 4rem;
  outline: none;
  padding: 0 1.6rem;
  color: #152935;
  -webkit-transition: all .4s;
  transition: all .4s;
  font-size: 1.4rem; }
  .text-box--multiline {
    height: 8rem;
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: 1.4rem; }
  .text-box::-webkit-input-placeholder {
    color: #cdd1d4;
    font-weight: 400;
    text-transform: capitalize;
    letter-spacing: 1px; }
  .text-box:focus:not(.text-box--error), .text-box:active:not(.text-box--error) {
    border-bottom: 1px solid #30588c; }
  .text-box--error {
    border-bottom: 1px solid #e0182d;
    box-shadow: 0 1px 0 0 #e0182d; }

.sidebar {
  width: 20rem;
  float: left;
  border-right: 1px solid #dfe3e6;
  height: 100vh;
  overflow: auto; }

.main {
  margin-left: 20rem;
  padding: 0 1rem;
  height: 100vh;
  overflow-y: auto;
  padding: 0 0 5rem 0; }

.document__header {
  padding: 3rem 2rem;
  background-color: rgba(85, 150, 230, 0.1);
  border-bottom: 1px solid #dfe3e6; }

.document__body {
  padding: 2rem; }
  .document__body p {
    padding: 1rem 0;
    margin: 1rem 0; }

.document__example-box {
  background-color: whitesmoke;
  padding: 1rem; }

.document__example-component {
  margin: 2rem 1rem 2rem 1rem;
  background-color: white;
  padding: 2rem; }

.codebox__button {
  background-color: transparent;
  border: none;
  text-decoration: underline;
  outline: none;
  cursor: pointer; }
  .codebox__button:hover {
    color: #3d70b2; }
  .codebox__button:active {
    color: #294c86; }

